(function() {
  'use strict';

  angular
    .module('angular')
    .controller('ProfileInfoController', ProfileInfoController);


  /** @ngInject */
  function ProfileInfoController(noosfero, $log, $stateParams, $scope) {
    var vm = this;
    vm.profile = null;
    vm.activities = [];
    activate();

    function activate() {
      vm.profile = $scope.vm.owner;
      noosfero.profiles.one(vm.profile.id).one('activities').get().then(function(result) {
        vm.activities = result.activities;
      });
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('angular')
    .controller('ProfileController', ProfileController);


  /** @ngInject */
  function ProfileController(noosfero, $log, $stateParams) {
    var vm = this;
    vm.boxes = [];
    activate();

    function activate() {
      noosfero.profiles.one().get({identifier: $stateParams.profile}).then(function(profiles) {
        vm.owner = profiles[0];
        return noosfero.boxes(vm.owner.id).one().get();
      }).then(function(response) {
        vm.boxes = response.boxes;
      });
    }

    vm.boxesOrder = function(box) {
      if(box.position==2) return 0;
      return box.position;
    }
  }
})();

