(function() {
  'use strict';

  angular
    .module('angular')
    .controller('ContentViewerController', ContentViewerController);


  /** @ngInject */
  function ContentViewerController(noosfero, $log, $stateParams, $scope) {
    var vm = this;
    vm.article = null;
    vm.profile = null;
    activate();

    function activate() {
      vm.profile = $scope.vm.owner;
      noosfero.profiles.one(vm.profile.id).one('articles').get({path: $stateParams.page}).then(function(articles) {
        vm.article = articles.article;
      });
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('angular')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, Restangular, Session) {
    Restangular.addFullRequestInterceptor(function(element, operation, route, url, headers) {
      if(Session.getCurrentUser()) {
        headers['Private-Token'] = Session.getCurrentUser().private_token;
      }
      return { headers: headers };
    });
  }

})();

(function() {
  'use strict';

  angular
    .module('angular')
    .config(routeConfig);

  function routeConfig($stateProvider, $urlRouterProvider) {
    $urlRouterProvider.when('/:profile', '/profile/:profile');
    $stateProvider
      .state('main', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'vm',
        resolve: {
          currentUser: function(AuthService) {
            return AuthService.loginFromCookie();
          }
        }
      })
      .state('main.profile', {
        url: ':profile',
        templateUrl: 'app/profile/profile.html',
        controller: 'ProfileController',
        controllerAs: 'vm'
      })
      .state('main.profile.info', {
        url: '^/profile/:profile',
        templateUrl: 'app/profile-info/profile-info.html',
        controller: 'ProfileInfoController',
        controllerAs: 'vm'
      })
      .state('main.profile.page', {
        url: '/{page:.*}',
        templateUrl: 'app/content-viewer/page.html',
        controller: 'ContentViewerController',
        controllerAs: 'vm'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

